@media only screen and (min-width: 0px) {
  .companydetailscard {
    margin-top: 4%;
    width: 95%;
  }
  .mrclass {
    margin-right: 0px !important;
  }
  .mlclass {
    margin-left: 0px !important;
  }
  .detailsclass {
    text-align: center;
  }
  .mbclass {
    margin-bottom: 10px !important;
  }
}
/* md */
@media only screen and (min-width: 960px) {
  .companydetailscard {
    margin-top: 2%;
    width: 90%;
  }
