.main {
  margin-left: auto;
  margin-right: auto;
  margin-top: 3%;
  width: 60%;
}
.listimg {
  width: 1050px;
  height: 200;
  flex-wrap: nowrap !important;
}
.rootlist {  margin: 40px;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  overflow: hidden;
}
.imgslide {
  height: 410px;
  width: 900px;
}

.uploadbtn{
  margin: 40px !important;
  width:11%;
  height: 100px;
  border-radius: 100% !important;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.uploadbox{
  flex-grow: 1;
  min-height: 400px;
  width: 100%;
  border-radius: 12px;
  border-color: rgb(74, 108, 170) !important;
  border-width: 2px !important;
  border-style:dashed;
  background-color: rgb(255, 255, 255);
}
.uploaded{
  width: 100px;
  height: 100px;
  border-radius: 20%;
  border-color: rgb(146, 146, 146) !important;
  border-width: 2px !important;
  border-style: solid;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
