.loginform {
  margin-left: auto;
  margin-right: auto;
}
.center {
  text-align: center;
  margin-top: 0;
  margin-bottom: 0;
}
.divtextcen {
  border-bottom: 1px solid #ddd;
  line-height: 0.1em !important;
}
.divtextcen span {
  font-weight: bold;
  background: #fff;
  padding: 0 7px;
}
.paperclass {
  padding: 20px !important;
}
.facebook-btn {
  background-color: #3a5794 !important;
  color: white !important;
}
.facebook-btn:hover {
  background-color: #2d4373 !important;
  color: white !important;
}
.google-btn {
  background-color: #1a8bf6 !important;
  color: white !important;
}
.google-btn:hover {
  background-color: #146cbf !important;
  color: white !important;
}

.passclass p {
  color: red !important;
}

@media only screen and (min-width: 0px) {
  .login-w {
    margin-top: 15%;
    width: 78%;
  }
  .register-w {
    margin-top: 10%;
    width: 78%;
  }
}
@media only screen and (min-width: 600px) {
  .login-w {
    margin-top: 8%;
    width: 350px;
  }
  .register-w {
    margin-top: 6%;
    width: 69%;
  }
}
@media only screen and (min-width: 960px) {
  .login-w {
    margin-top: 4.5%;
    width: 420px;
  }
  .register-w {
    margin-top: 3%;
    width: 90%;
  }
}
