.weblinkclass {
  text-decoration: none !important;
  color: #21a3f6 !important;
}
.weblinkclass:hover {
  color: #227df4 !important;
}
.imgss {
  height: 500px;
  width: 100%;
  position: relative;
}
.paddall {
  padding: 10px !important;
}
.imgList {
  width: 100%;
  height: 200;
  flex-wrap: nowrap !important;
}
.rootimglist {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  overflow: hidden;
}
.main {
  margin-top: 3%;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
}
.profilepic {
  padding: 10px;
  width: 100px;
  height: 100px;
}
.visitbtn {
  margin-top: 70px !important;
  margin-right: 10px !important;
}
.avatarsize {
  height: 30px !important;
  width: 30px !important;
}
.profpost {
  width: 100px !important;
  height: 100px !important;
}
.profreview {
  margin: 10px !important;
  width: 60px !important;
  height: 60px !important;
}
@media only screen and (min-width: 0px) {
  .companydetailscard {
    margin-top: 4%;
    width: 95%;
  }
  .detailsclass {
    text-align: center;
  }
}
@media only screen and (min-width: 960px) {
  .companydetailscard {
    margin-top: 2%;
    width: 80%;
  }
  .detailsclass {
    text-align: start;
  }
}
@media only screen and (min-width: 1280px) {
  .companydetailscard {
    margin-top: 2%;
    width: 75%;
  }
  .detailsclass {
    text-align: start;
  }
}
.listimages {
  width: 100%;
  height: 200px;
}
